.container {
	max-width: 480px;
	padding-top: 84px;
	padding-bottom: 84px;
	padding-left: 31px;
	padding-right: 31px;
	margin: 0 auto;

	@media screen and (min-width: 768px) and (max-width: 1199px) {
		max-width: 768px;
		padding-top: 100px;
		padding-bottom: 100px;
		padding-left: 39px;
		padding-right: 39px;
	}
	@media screen and (min-width: 1200px) {
		max-width: 1200px;
		padding-top: 130px;
		padding-bottom: 130px;
		padding-left: 15px;
		padding-right: 15px;
	}
}
